import React, {Component} from 'react';
import Grid from './parts/Grid';


export default class Map extends Component{
    constructor(props){
        super(props);

        let clientWidth = document.documentElement.clientWidth;
        let clientHeight = document.documentElement.clientHeight;

        this.state = {
            countColumn: Math.floor(clientHeight / 50),
            countRows: Math.floor(clientWidth / 50),
            gridArray: [],
            test: false,

        };
    }

    componentWillMount(){
        this.setState({
            gridArray: this.createGridArray(),
            test: true
        });
    }

    createGridArray(rows = this.state.countRows, column = this.state.countColumn){
        let array = [];
        for(let i = 0;i < column;i++){
            array[i] = [];
            for(let j = 0;j < rows;j++){
                array[i][j] = i;
            }
        }
        return array;
    }

    clickHandle(){
        this.props.getMap();
    }

    render(){
        return(
            <div className="map" onClick={this.clickHandle.bind(this)}>
                {/*{`${this.state.countRows} : ${this.state.countColumn}`}*/}
                <Grid gridArray={this.state.gridArray}/>
            </div>
        );
    }
}